.toDo {
    &__head {
        padding: 40px 0;
        @include linearGradient(135deg, rgba($primary, 1) 50%, rgba($secondary, 1) 100%);

        &Top {
            @extend %flexbox;
            justify-content: center;
            align-items: center;
            flex-flow: column;

            .inputBox {
                margin-top: 20px;

                input {
                    width: 450px;
                    height: 35px;
                    border: none;
                    box-sizing: border-box;
                    background-color: $white;
                    border: 1px solid rgba($black, .5);
                    @include border-radius(8px);
                    padding: 8px 10px;
                    font-size: 14px;
                    font-family: $medium_font-family_secondary;
                    font-weight: $medium_font-weight;
                }
            }

            span {
                position: relative;
                font-size: 28px;
                font-family: $bold_font-family_secondary;
                font-weight: $bold_font-weight;
                text-transform: uppercase;

                &:not(:last-child) {
                    margin-right: 5px;
                    padding-right: 5px;

                    &::after {
                        content: ',';
                        position: absolute;
                        right: 0;
                        bottom: 0;
                    }
                }
            }

            .btnBox {
                @extend %flexbox;
                align-items: CENTER;
                justify-content: center;
                margin-top: 20px;

                .btn {
                    height: 30px;
                    min-width: 100px;
                    width: 100px;
                    background-color: $complementray;
                    color: #323232;

                    &:hover {
                        background-color: $white;
                        color: $secondary;
                    }

                    &:not(:last-child) {
                        margin-right: 15px;
                    }

                    span {
                        text-transform: capitalize;
                        font-size: 14px;
                    }
                }
            }
        }
    }

    &__body {
        padding: 30px 0;
    }

    &__item {
        @extend %flexbox;
        align-items: center;
        justify-content: space-between;
        background: $white;
        border: 1px solid rgba($black, 0.2);
        padding: 15px 10px;
        @include border-radius(8px);
        @include transition(all,625ms,ease-in-out);
        .left{
            @extend %flexbox;
            align-items: center;
            justify-content: flex-start;
            input[type="checkbox"] {
                width: 30px;
                height: 30px;
                cursor: pointer;
            }
            span{
                font-size: $medium_font-family_secondary;
                font-weight: $medium_font-weight;
                font-size: 16px;
                margin-left: 10px;
            }
        }
        .delete{
            position: relative;
            display: block;
            width: 25px;
            flex: 0 0 25px;
            height: 25px;
            border: 1px solid rgba($black, 0.5);
            @include border-radius(50%);
            cursor: pointer;
            &::After{
                content: 'x';
                position: absolute;
                width: 100%;
                height: 100%;
                @extend %flexbox;
                align-items: center;
                justify-content: center;
                color: rgba($black, 0.5);
                font-size: $medium_font-family_secondary;
                font-weight: $medium_font-weight;
                text-transform: uppercase;
            }
        }
        &:hover{
            @include box-shadow(0,0,8px,-3px,rgba($black, .5));
        }
    }
}